<template>
    <div>
        <el-card class="box-card">
        <el-avatar
        shape="square"
        :size="150"
        fit="cover"
        :src="user.photo"
        >
        </el-avatar>
        <p @click="$refs.refFile.click()" class="btnImage"></p>
        <input type="file" hidden ref="refFile" @change="onFileChang" >

        </el-card>

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        @opened="onDialogOpened"
        @closed="onDialogClosed"
        >
        <div class="images">
            <img class="images-item" :src="previewImag" ref="images-item">
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button 
            :loading="updateLoading"
            type="primary" @click="onUpdatePhoto"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';
export default {
    data(){
        return{
            user:{
                email:'',
                id:null,
                intro:'',
                mobile:'',
                name:'',
                photo:''
            },
            dialogVisible:false,
            previewImag:'',      // 
            cropper:null,        // 
            updateLoading:false
        }
    },
    methods:{
        add(){
            console.log('1');
        },
        changes(){
            console.log('2');
        },
        // 
        onFileChang(){
            //  
            const file = this.$refs.refFile
            const blobData = window.URL.createObjectURL(file.files[0])
            this.previewImag = blobData
            this.dialogVisible = true
            this.$refs.refFile.value = ''
        },
        onDialogOpened(){
            const image = this.$refs['images-item']
            this.cropper = new Cropper(image, {
            aspectRatio: 1,     //   3/2
            viewMode:1,         //
            dragMode:'none'     //
            });
        },
        onDialogClosed(){
            //  
            this.cropper.destroy()
        },
        onUpdatePhoto(){
            this.updateLoading = true
            //  
            this.cropper.getCroppedCanvas().toBlob(blob =>{
                const fd = new FormData()
                fd.append('photo',blob)
                //   fd
                // updateUserPhoto(fd).then(res =>{
                    //  
                    this.dialogVisible = false
                    //  1 blob 
                    this.user.photo = window.URL.createObjectURL(blob)

                    //  2
                    // this.user.photo = res.data.data.photo
                    this.updateLoading = false
                    this.$message({
                        message:'',
                        type:'success'
                    })
                // })
            })
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        }
    }
}
</script>
<style lang="less" scoped>
.btnImage{
    cursor: pointer;
}
</style>